import React from "react";
import { useDebounce } from "../../util";
import { SearchPanel } from "./SearchPanel";
import { List } from "./list";
import styled from "@emotion/styled";
import { useProjects } from "../../util/project";
import { Row, Typography } from "antd";
import { useUser } from "../../util/user";
import { useProjectModal, useProjectsSeachParams } from "./utils";
import {
  ButtonNoPadding,
  ErrorBox,
  ScreenContainer,
} from "../../components/lib";

// https://codesandbox.io/s/keen-wave-tlz9s?file=/src/App.js
export const ProjectListScreen = () => {
  // 基本类型，可以放到依赖里；组件状态，可以放都依赖；非组件状态的对象，绝不可以放在依赖里
  const [params, setParams] = useProjectsSeachParams();
  const {
    isLoading,
    error,
    data: list,
  } = useProjects(useDebounce(params, 200));
  const { data: users } = useUser();
  let { open } = useProjectModal();
  //useDocumentTitle("项目列表", true);

  return (
    <ScreenContainer>
      <Row justify={"space-between"}>
        <h1>项目列表</h1>
        <ButtonNoPadding onClick={open} type={"link"}>
          创建项目
        </ButtonNoPadding>
      </Row>
      <SearchPanel params={params} setParams={setParams} users={users || []} />
      <ErrorBox error={error}></ErrorBox>

      <List dataSource={list || []} users={users || []} loading={isLoading} />
    </ScreenContainer>
  );
};

ProjectListScreen.whyDidYouRender = false;

const Container = styled.div`
  padding: 3.2rem 0;
`;
