import styled from "@emotion/styled";
import { Drawer, Spin, Form, Input, Button } from "antd";
import { useForm } from "antd/lib/form/Form";
import React, { useEffect } from "react";
import { ErrorBox } from "../../components/lib";
import { UserSelect } from "../../components/user-select";
import { useAddProject, useEditProject } from "../../util/project";
import { useProjectModal, useProjectsQueryKey } from "./utils";

export const ProjectModal = () => {
  let { close, projectModalOpen, open, isLoading, startEdit, editingProject } =
    useProjectModal();
  // 判断是新增页面还是编辑页面
  const useMutationProject = editingProject ? useEditProject : useAddProject;

  const {
    mutateAsync,
    error,
    isLoading: mutationLoading,
  } = useMutationProject(useProjectsQueryKey());

  // design组件创造一个form实例
  const [form] = useForm();

  const title = editingProject ? "编辑项目" : "新建项目";

  const onFinish = (value: any) => {
    mutateAsync({ ...editingProject, ...value }).then(() => {
      form.resetFields();
      close();
    });
  };

  const onClose = () => {
    form.resetFields();
    close();
  };

  useEffect(() => {
    form.setFieldsValue(editingProject);
  }, [editingProject, form]);

  return (
    <Drawer
      visible={projectModalOpen}
      onClose={onClose}
      width={"100%"}
      forceRender={true}
    >
      <Container>
        {isLoading ? (
          <Spin size={"large"} />
        ) : (
          <>
            <h1>{title}</h1>
            <ErrorBox error={error} />
            <Form
              form={form}
              layout={"vertical"}
              style={{ width: "40rem" }}
              onFinish={onFinish}
            >
              <Form.Item
                label={"名称"}
                name={"name"}
                rules={[{ required: true, message: "请输入项目名" }]}
              >
                <Input placeholder={"请输入项目名称"} />
              </Form.Item>
              <Form.Item
                label={"部门"}
                name={"organization"}
                rules={[{ required: true, message: "请输入部门名" }]}
              >
                <Input placeholder={"请输入部门名"} />
              </Form.Item>
              <Form.Item label={"负责人"} name={"personId"}>
                <UserSelect defaultOptionsName={"负责人"} />
              </Form.Item>
              <Form.Item style={{ textAlign: "right" }}>
                <Button
                  loading={mutationLoading}
                  type={"primary"}
                  htmlType={"submit"}
                >
                  提交
                </Button>
              </Form.Item>
            </Form>
          </>
        )}
      </Container>
    </Drawer>
  );
};

const Container = styled.div`
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
