import React, { ReactNode, useState } from "react";
import { useQueryClient } from "react-query";
import * as auth from "../auth-providers";
import { FullPageErrorFallback, FullPageLoading } from "../components/lib";
import { user } from "../types/user";
import { useMount } from "../util";
import { http } from "../util/http";
import { useAsync } from "../util/use-async";
interface AuthForm {
  username: string;
  password: string;
  cpassword?: string;
}

const bootstrapUser = async () => {
  let user = null;
  const token = auth.getToken();
  if (token) {
    const data = await http("me", { token });
    console.log("data", data);
    user = data.user;
  }
  return user;
};

const AuthContext = React.createContext<
  | {
      user: user | null | undefined;
      login: (form: AuthForm) => Promise<void>;
      register: (form: AuthForm) => Promise<void>;
      logout: () => Promise<void>;
    }
  | undefined
>(undefined);
AuthContext.displayName = "AuthContext";

// children就是从父组件中传入进来的App组件 相当于children属性中的children组件
export const AuthProvider = ({ children }: { children: ReactNode }) => {
  // const [user, setUser] = useState<user | undefined>(undefined);

  let {
    isIdle,
    isLoading,
    isError,
    run,
    setData: setUser,
    data: user,
    error,
  } = useAsync<user | undefined>();
  const queryClient = useQueryClient();

  const login = (form: AuthForm) => auth.login(form).then(setUser);
  const register = (form: AuthForm) => auth.register(form).then(setUser);
  const logout = () =>
    auth.logout().then(() => {
      queryClient.clear();
      setUser(undefined);
    });

  useMount(() => {
    run(bootstrapUser());
  });
  if (isIdle || isLoading) {
    return <FullPageLoading />;
  }

  if (isError) {
    return <FullPageErrorFallback error={error} />;
  }

  return (
    <AuthContext.Provider
      value={{ user, login, register, logout }}
      children={children}
    />
  );
};

export const useAuth = () => {
  // context就是AuthContext.Provider中的value的值
  const context = React.useContext(AuthContext);
  if (!context) {
    throw Error("useAuth必须在AuthProvider中使用");
  }
  return context;
};
