import { useEffect, useRef, useState } from "react";

export const isFalsy = (value: unknown) => (value === 0 ? false : !value);

// 如下，aobject的时候，给a赋值的方式如下三种都是可以的

// let a: object;
// a = () => { };
// a = { name: "123" };
// a = new RegExp('')

// // b限制对象的类型
// let b: { [key: string]: unknown };
// b = { name: '123' }
// b = () = {}

export const cleanObject = (object: { [key: string]: unknown }) => {
  const result = { ...object };
  Object.keys(result).forEach((key) => {
    const value = result[key];
    if (isFalsy(value)) {
      delete result[key];
    }
  });
  return result;
};

export const useMount = (callback: () => void) => {
  useEffect(() => {
    callback();
    // TODO 依赖里加入callback会造成无线循环，这个和useMemo和useCallBack有关系
    //eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
};

// 使用自定义hook，添加自动以的防抖函数
export const useDebounce = <V>(value: V, delay: number) => {
  const [debounceValue, setDebounceValue] = useState(value);
  useEffect(() => {
    let tiemout = setTimeout(() => setDebounceValue(value), delay);
    return () => clearTimeout(tiemout);
  }, [value, delay]);
  return debounceValue;
};

export const useArray = <T>(initialArray: T[]) => {
  const [value, setValue] = useState(initialArray);
  return {
    value,
    setValue,
    add: (item: T) => setValue([...value, item]),
    clear: () => setValue([]),
    removeIndex: (index: number) => {
      let copy = [...value];
      copy.splice(index, 1);
      setValue(copy);
    },
  };
};

export const useDocumentTitle = (title: string, keepOnUnmout = true) => {
  const oldTitle = useRef(document.title).current;

  useEffect(() => {
    document.title = title;
  }, [title]);

  // 当离开页面时，就返回到原来的页面的title
  useEffect(() => {
    return () => {
      if (!keepOnUnmout) {
        document.title = oldTitle;
      }
    };
  }, [keepOnUnmout, oldTitle]);
};

export const resetRoute = () => (window.location.href = window.location.origin);

/**
 * 返回组件的挂载状态，如果还没有挂载或者已经卸载，返回false，反之，返回true
 */

export const useMountedRef = () => {
  const mountedRef = useRef(false);

  useEffect(() => {
    mountedRef.current = true;
    return () => {
      mountedRef.current = false;
    };
  });
  return mountedRef;
};
