import { useMemo } from "react";
import { useSearchParams } from "react-router-dom";
import { useProject } from "../../util/project";
import { useSetUrlSearchParam, useUrlQueryParam } from "./../../util/url";

export const useProjectsSeachParams = () => {
  const [params, setParams] = useUrlQueryParam(["name", "personId"]);
  return [
    useMemo(
      () => ({ ...params, personId: Number(params.personId) || undefined }),
      [params]
    ),
    setParams,
  ] as const;
};

export const useProjectsQueryKey = () => {
  const [params] = useProjectsSeachParams();
  return ["projects", params];
};

export const useProjectModal = () => {
  // useSearchParams会把数据转换成string的形式
  const [{ projectCreate }, setProjectCreate] = useUrlQueryParam([
    "projectCreate",
  ]);

  // 编辑页面时的编辑数据的id
  const [{ editingProjectId }, setEditinProjectId] = useUrlQueryParam([
    "editingProjectId",
  ]);

  const setSearchParam = useSetUrlSearchParam();

  const { data: editingProject, isLoading } = useProject(
    Number(editingProjectId)
  );

  const open = () => setProjectCreate({ projectCreate: true });
  const close = () =>
    setSearchParam({ projectCreate: "", editingProjectId: "" });

  const startEdit = (id: number) =>
    setEditinProjectId({ editingProjectId: id });

  return {
    open,
    close,
    projectModalOpen: projectCreate === "true" || Boolean(editingProjectId),
    isLoading,
    startEdit,
    editingProject,
  };
};
