import { Dropdown, Table, TableProps, Menu } from "antd";
import React from "react";
import dayjs from "dayjs";
import { Link } from "react-router-dom";
import { Pin } from "../../components/pin";
import { useDeleteProject, useEditProject } from "../../util/project";
import { ButtonNoPadding } from "../../components/lib";
import { useProjectModal, useProjectsQueryKey } from "./utils";
import { Project } from "../../types/projects";
import { user } from "../../types/user";

interface ListsProps extends TableProps<Project> {
  users: user[];
}

export const List = ({ users, ...props }: ListsProps) => {
  const { mutateAsync } = useEditProject(useProjectsQueryKey());
  const pinProject = (id: number) => (pin: boolean) => mutateAsync({ id, pin });
  return (
    <Table
      pagination={false}
      rowKey={(columns) => columns.id}
      columns={[
        {
          title: <Pin checked={true} disabled={true} />,
          render(value, project) {
            return (
              <Pin
                checked={project.pin}
                onCheckedChange={pinProject(project.id)}
              />
            );
          },
        },
        {
          title: "名称",
          sorter: (a, b) => a.name.localeCompare(b.name),
          render(value, project) {
            return <Link to={`${project.id}`}>{project.name}</Link>;
          },
        },
        { title: "部門", dataIndex: "organization" },
        {
          title: "负责人",
          render: (text, project) => {
            return (
              <span>
                {users.find((user) => user.id === project.personId)?.name ||
                  "未知"}
              </span>
            );
          },
        },
        {
          title: "创建时间",
          render: (value, project) => {
            return (
              <span>
                {project.created
                  ? dayjs(project.created).format("YYYY-MM-DD")
                  : "无"}
              </span>
            );
          },
        },
        {
          render(value, project) {
            return <More project={project} />;
          },
        },
      ]}
      {...props}
    />
  );
};

const More = ({ project }: { project: Project }) => {
  const { mutateAsync: deleteProject } = useDeleteProject(
    useProjectsQueryKey()
  );
  const { startEdit } = useProjectModal();
  return (
    <Dropdown
      overlay={
        <Menu>
          <Menu.Item key={"edit"} onClick={() => startEdit(project.id)}>
            编辑
          </Menu.Item>
          <Menu.Item
            key={"delete"}
            onClick={() => deleteProject({ id: project.id })}
          >
            删除
          </Menu.Item>
        </Menu>
      }
    >
      <ButtonNoPadding type={"link"}>...</ButtonNoPadding>
    </Dropdown>
  );
};
