/**
 * 在本地对排序进行乐观更新
 * @param fromId 要排序的项目的id
 * @param type 'before' | 'after'
 * @param referenceId 参照id
 * @param list 要排序的列表, 比如tasks, kanbans
 */

export const reorder = ({
  fromId,
  type,
  referenceId,
  list,
}: {
  fromId: number;
  type: "before" | "after";
  referenceId: number;
  list: { id: number }[];
}) => {
  const copiedList = [...list];
  // 找到formId对应的项目下标
  const movingItemIndex = copiedList.findIndex((item) => item.id === fromId);
  if (!referenceId) {
    return inserAfter([...copiedList], movingItemIndex, copiedList.length - 1);
  }
  const targetIndex = copiedList.findIndex((item) => item.id === referenceId);
  const insert = type === "before" ? insertBefore : inserAfter;
  return insert([...copiedList], movingItemIndex, targetIndex);
};
/**
 * 在list中，把from放在to的前边
 * @param list
 * @param from
 * @param to
 */
const insertBefore = (list: unknown[], from: number, to: number) => {
  const toItem = list[to];
  const removeItem = list.splice(from, 1)[0];
  const toIndex = list.indexOf(toItem);
  list.splice(toIndex, 0, removeItem);
  return list;
};

/**
 * 在list中，把from放在to的后面
 * @param list
 * @param from
 * @param to
 */
const inserAfter = (list: unknown[], from: number, to: number) => {
  const toItem = list[to];
  const removeItem = list.splice(from, 1)[0];
  const toIndex = list.indexOf(toItem);
  list.splice(toIndex + 1, 0, removeItem);
  return list;
};
