/** @jsxImportSource @emotion/react */
import React from "react";
import { Input, Form } from "antd";
import { UserSelect } from "../../components/user-select";
import { Project } from "../../types/projects";
import { user } from "../../types/user";

interface SearchProps {
  params: Partial<Pick<Project, "name" | "personId">>;
  setParams: (params: SearchProps["params"]) => void;
  users: user[];
}

export const SearchPanel = ({ params, setParams, users }: SearchProps) => {
  return (
    <Form layout="inline" css={{ marginBottom: "2rem" }}>
      <Form.Item>
        <Input
          placeholder={"项目名"}
          type="text"
          value={params.name}
          onChange={(evt) => setParams({ ...params, name: evt.target.value })}
        />
      </Form.Item>
      <Form.Item>
        <UserSelect
          defaultOptionsName={"负责人"}
          value={params.personId}
          onChange={(value) => {
            return setParams({ ...params, personId: value });
          }}
        ></UserSelect>
      </Form.Item>
    </Form>
  );
};
