import { Button, List, Modal } from "antd";
import React, { useState } from "react";
import { Row, ScreenContainer } from "../../components/lib";
import { useDeleteEpic, useEpics } from "../../util/epic";
import { useTasks } from "../../util/tasks";
import { useProjectInUrl } from "../kanban/utils";
import { useEpickSearchParams, useEpicQueryKey } from "./utils";
import dayjs from "dayjs";
import { Link } from "react-router-dom";
import { Epic } from "../../types/epics";
import { CreateEpic } from "./create-epic";

export const EpicScreen = () => {
  const { data: currentProjet } = useProjectInUrl();
  const { data: epics } = useEpics(useEpickSearchParams());
  const { data: tasks = [] } = useTasks({ projectId: currentProjet?.id });
  const { mutate: deleteEpic } = useDeleteEpic(useEpicQueryKey());

  const [epicCreateOpen, setEpicCreateOpen] = useState(false);

  const confirDeleteEpic = (epic: Epic) => {
    Modal.confirm({
      title: `确定删除项目组：${epic.name}`,
      content: "点击确定删除",
      okText: "确定",
      onOk() {
        deleteEpic({ id: epic.id });
      },
    });
  };
  return (
    <ScreenContainer>
      <Row between={true}>
        <h1>{currentProjet?.name}</h1>
        <Button type="link" onClick={() => setEpicCreateOpen(true)}>
          创建任务组
        </Button>
      </Row>
      <List
        style={{ overflow: "scroll", flex: 1 }}
        dataSource={epics}
        itemLayout={"vertical"}
        renderItem={(epic) => (
          <List.Item>
            <List.Item.Meta
              title={
                <Row between={true}>
                  <span>{epic.name}</span>
                  <Button type="link" onClick={() => confirDeleteEpic(epic)}>
                    删除
                  </Button>
                </Row>
              }
              description={
                <div>
                  <div>开始时间：{dayjs(epic.start).format("YYYY-MM-DD")}</div>
                  <div>结束时间：{dayjs(epic.end).format("YYYY-MM-DD")}</div>
                </div>
              }
            />
            <div>
              {tasks
                ?.filter((task) => task.epicId === epic.id)
                .map((item) => (
                  <Link
                    to={`/projects/${currentProjet?.id}/kanban?editingTaskId=${item.id}`}
                    key={item.id}
                  >
                    {item.name}
                  </Link>
                ))}
            </div>
          </List.Item>
        )}
      ></List>
      <CreateEpic
        onClose={() => setEpicCreateOpen(false)}
        visible={epicCreateOpen}
      ></CreateEpic>
    </ScreenContainer>
  );
};
